.wp-block-blocks-gamestore-block-slider {
  margin-top: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;
  margin-bottom: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;

  .slider-inner-content {
    text-align: center;

    h2 {
      line-height: 1.4;
      background: var(--accent-gradient);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      margin-top: 0;
      margin-bottom: 24px;
      text-align: center;
      width: fit-content;
      position: relative;
      z-index: 1;
      display: inline-block;
    }
  
    p {
      margin:0;
      max-width: 980px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 48px;
      position: relative;
      z-index: 1;
      color: var(--text-secondary, rgba(14, 13, 15, 0.70));
    }

    .slider-media {
      padding-top: 100px;
      padding-bottom: 100px;

      > .swiper-wrapper {
        display: flex;

        .swiper-slide {
          position: relative;
          transition: transform 0.9s ease-in-out;
          width: 380px;

          &.swiper-slide-active {
            transform: scale(1.8);
            z-index: 9;
          }
          &.swiper-slide-prev,
          &.swiper-slide-next {
            transform: scale(1.5);
            z-index: 8;
          }

          img {
            max-width: 100%;
            height: auto;
            border-radius: 12px;
          }

          .blur-image {
            position: absolute;
            width: 100%;
            height: 100%;
            top:0;
            left:0;
            z-index: 1;
            filter: blur(24px);
          }
          .original-image {
            z-index: 2;
            position: relative;
          }
        }
      }
    }
  }

}